// å®šä¹‰æŒ‡ä»¤ç±»å‹
export interface Instruction {
  action: 'add' | 'complete' | 'delete' | 'list' | 'clear_completed' | 'clear_all';
  task?: string;
  id?: number;
  response?: string;
}

// å®šä¹‰å¾…åŠäº‹é¡¹ç±»å‹
export interface Todo {
  id: number;
  text: string;
  completed: boolean;
}

// å®šä¹‰æ‰§è¡Œç»“æœç±»å‹
export interface ExecutionResult {
  success: boolean;
  message?: string;
  data?: Todo[] | Todo | boolean;
}

// å®šä¹‰å‡½æ•°æ˜ å°„è¡¨ç±»å‹
export type TodoFunction = (params?: string | number | undefined) => ExecutionResult | Todo[] | boolean | void;

// æŒ‡ä»¤æ˜ å°„å™¨ç±»
export class InstructionMapper {
  private functionMap: Map<string, TodoFunction>;
  
  constructor() {
    this.functionMap = new Map();
  }

  // æ³¨å†Œå‡½æ•°åˆ°æ˜ å°„è¡¨
  registerFunction(action: string, func: TodoFunction) {
    this.functionMap.set(action, func);
  }

  // æ‰§è¡ŒæŒ‡ä»¤æ˜ å°„çš„å‡½æ•°
  executeInstruction(instruction: Instruction): ExecutionResult {
    const func = this.functionMap.get(instruction.action);
    
    if (!func) {
      console.warn(`æœªæ‰¾åˆ°æ“ä½œ "${instruction.action}" å¯¹åº”çš„å‡½æ•°`);
      return { 
        success: false, 
        message: `ä¸æ”¯æŒçš„æ“ä½œ: ${instruction.action}` 
      };
    }

    try {
      // æ ¹æ®ä¸åŒçš„æ“ä½œç±»å‹ä¼ é€’ä¸åŒçš„å‚æ•°
      let result: ExecutionResult | Todo[] | boolean | void;
      
      switch (instruction.action) {
        case 'add':
          result = func(instruction.task);
          break;
        case 'complete':
        case 'delete':
          result = func(instruction.id || instruction.task);
          break;
        case 'list':
        case 'clear_completed':
        case 'clear_all':
          result = func();
          break;
        default:
          result = func();
      }

      // æ ‡å‡†åŒ–è¿”å›ç»“æœ
      if (typeof result === 'object' && result !== null && 'success' in result) {
        return result as ExecutionResult;
      } else {
        return {
          success: true,
          message: `æ“ä½œ "${instruction.action}" æ‰§è¡ŒæˆåŠŸ`,
          data: result as Todo[] | Todo | boolean
        };
      }
    } catch (error) {
      console.error('æ‰§è¡ŒæŒ‡ä»¤æ—¶å‘ç”Ÿé”™è¯¯:', error);
      return { 
        success: false, 
        message: `æ‰§è¡Œæ“ä½œ "${instruction.action}" æ—¶å‘ç”Ÿé”™è¯¯` 
      };
    }
  }

  // è·å–æ‰€æœ‰æ³¨å†Œçš„å‡½æ•°
  getRegisteredActions(): string[] {
    return Array.from(this.functionMap.keys());
  }

  // æ£€æŸ¥æ˜¯å¦æ”¯æŒæŸä¸ªæ“ä½œ
  supportsAction(action: string): boolean {
    return this.functionMap.has(action);
  }
}

// åˆ›å»ºå…¨å±€å®ä¾‹
export const instructionMapper = new InstructionMapper();

// è¾…åŠ©å‡½æ•°ï¼šæ ¹æ®ä»»åŠ¡åç§°æŸ¥æ‰¾todoé¡¹
export function findTodoByText(todos: Todo[], taskText: string): Todo | undefined {
  return todos.find(todo => 
    todo.text.toLowerCase().includes(taskText.toLowerCase()) ||
    taskText.toLowerCase().includes(todo.text.toLowerCase())
  );
}

// è¾…åŠ©å‡½æ•°ï¼šæ ¹æ®IDæŸ¥æ‰¾todoé¡¹
export function findTodoById(todos: Todo[], id: number): Todo | undefined {
  return todos.find(todo => todo.id === id);
} 