import { useState, useCallback } from 'react';

// è®°å¿†æ¡ç›®æ¥å£
export interface MemoryEntry {
  id: string;
  timestamp: number;
  type: 'user_message' | 'ai_response' | 'action_executed' | 'context_reference';
  content: string;
  metadata?: {
    action?: string;
    taskId?: number;
    taskText?: string;
    reference?: string;
  };
}

// ä¸Šä¸‹æ–‡ç±»å‹
export interface Context {
  recentActions: Array<{
    action: string;
    taskId?: number;
    taskText?: string;
    timestamp: number;
  }>;
  lastAddedTask?: {
    id: number;
    text: string;
    timestamp: number;
  };
  recentMessages: Array<{
    content: string;
    timestamp: number;
    type: 'user' | 'ai';
  }>;
}

export function useMemory() {
  const [memory, setMemory] = useState<MemoryEntry[]>([]);
  const [context, setContext] = useState<Context>({
    recentActions: [],
    recentMessages: []
  });

  // æ·»åŠ è®°å¿†æ¡ç›®
  const addMemoryEntry = useCallback((entry: Omit<MemoryEntry, 'id' | 'timestamp'>) => {
    const newEntry: MemoryEntry = {
      ...entry,
      id: `mem_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`,
      timestamp: Date.now()
    };
    
    setMemory(prev => [...prev.slice(-49), newEntry]); // ä¿æŒæœ€è¿‘50æ¡è®°å½•
    return newEntry;
  }, []);

  // è®°å½•ç”¨æˆ·æ¶ˆæ¯
  const recordUserMessage = useCallback((message: string) => {
    addMemoryEntry({
      type: 'user_message',
      content: message
    });

    // æ›´æ–°ä¸Šä¸‹æ–‡ä¸­çš„æœ€è¿‘æ¶ˆæ¯
    setContext(prev => ({
      ...prev,
      recentMessages: [
        ...prev.recentMessages.slice(-9), // ä¿æŒæœ€è¿‘10æ¡æ¶ˆæ¯
        {
          content: message,
          timestamp: Date.now(),
          type: 'user'
        }
      ]
    }));
  }, [addMemoryEntry]);

  // è®°å½•AIå›å¤
  const recordAIResponse = useCallback((response: string) => {
    addMemoryEntry({
      type: 'ai_response',
      content: response
    });

    // æ›´æ–°ä¸Šä¸‹æ–‡ä¸­çš„æœ€è¿‘æ¶ˆæ¯
    setContext(prev => ({
      ...prev,
      recentMessages: [
        ...prev.recentMessages.slice(-9),
        {
          content: response,
          timestamp: Date.now(),
          type: 'ai'
        }
      ]
    }));
  }, [addMemoryEntry]);

  // è®°å½•æ‰§è¡Œçš„åŠ¨ä½œ
  const recordAction = useCallback((action: string, taskId?: number, taskText?: string) => {
    addMemoryEntry({
      type: 'action_executed',
      content: `æ‰§è¡ŒåŠ¨ä½œ: ${action}${taskText ? ` - ${taskText}` : ''}`,
      metadata: {
        action,
        taskId,
        taskText
      }
    });

    // æ›´æ–°ä¸Šä¸‹æ–‡ä¸­çš„æœ€è¿‘åŠ¨ä½œ
    setContext(prev => ({
      ...prev,
      recentActions: [
        ...prev.recentActions.slice(-9), // ä¿æŒæœ€è¿‘10ä¸ªåŠ¨ä½œ
        {
          action,
          taskId,
          taskText,
          timestamp: Date.now()
        }
      ],
      // å¦‚æœæ˜¯æ·»åŠ ä»»åŠ¡ï¼Œæ›´æ–°æœ€åæ·»åŠ çš„ä»»åŠ¡
      ...(action === 'add' && taskId && taskText ? {
        lastAddedTask: {
          id: taskId,
          text: taskText,
          timestamp: Date.now()
        }
      } : {})
    }));
  }, [addMemoryEntry]);

  // è®°å½•ä¸Šä¸‹æ–‡å¼•ç”¨
  const recordContextReference = useCallback((reference: string, content: string) => {
    addMemoryEntry({
      type: 'context_reference',
      content,
      metadata: {
        reference
      }
    });
  }, [addMemoryEntry]);

  // è§£æä¸Šä¸‹æ–‡å¼•ç”¨ï¼ˆå¦‚"åˆšæ‰é‚£ä¸ª"ã€"æœ€åä¸€ä¸ª"ç­‰ï¼‰
  const resolveContextReference = useCallback((userInput: string) => {
    const input = userInput.toLowerCase();
    let resolvedContext = '';
    
    // æ£€æŸ¥æ˜¯å¦å¼•ç”¨äº†"åˆšæ‰"ã€"æœ€å"ã€"ä¸Šä¸€ä¸ª"ç­‰
    if (input.includes('åˆšæ‰') || input.includes('æœ€å') || input.includes('ä¸Šä¸€ä¸ª') || input.includes('ä¸Šä¸ª')) {
      if (input.includes('æ·»åŠ ') || input.includes('ä»»åŠ¡')) {
        // å¼•ç”¨æœ€åæ·»åŠ çš„ä»»åŠ¡
        if (context.lastAddedTask) {
          resolvedContext = `å¼•ç”¨æœ€åæ·»åŠ çš„ä»»åŠ¡: "${context.lastAddedTask.text}" (ID: ${context.lastAddedTask.id})`;
          recordContextReference('last_added_task', resolvedContext);
          return {
            type: 'task_reference',
            taskId: context.lastAddedTask.id,
            taskText: context.lastAddedTask.text,
            resolvedText: resolvedContext
          };
        }
      } else {
        // å¼•ç”¨æœ€åçš„åŠ¨ä½œ
        const lastAction = context.recentActions[context.recentActions.length - 1];
        if (lastAction) {
          resolvedContext = `å¼•ç”¨æœ€åçš„åŠ¨ä½œ: ${lastAction.action}${lastAction.taskText ? ` - ${lastAction.taskText}` : ''}`;
          recordContextReference('last_action', resolvedContext);
          return {
            type: 'action_reference',
            action: lastAction.action,
            taskId: lastAction.taskId,
            taskText: lastAction.taskText,
            resolvedText: resolvedContext
          };
        }
      }
    }

    // æ£€æŸ¥æ˜¯å¦å¼•ç”¨äº†"å†"ã€"å†åŠ "ç­‰
    if (input.includes('å†åŠ ') || input.includes('å†æ·»åŠ ')) {
      const lastAddAction = context.recentActions
        .slice()
        .reverse()
        .find(action => action.action === 'add');
      
      if (lastAddAction) {
        resolvedContext = `åŸºäºä¸Šæ¬¡æ·»åŠ çš„ä»»åŠ¡æ¨¡å¼: "${lastAddAction.taskText}"`;
        recordContextReference('repeat_add_pattern', resolvedContext);
        return {
          type: 'repeat_pattern',
          action: 'add',
          referenceTask: lastAddAction.taskText,
          resolvedText: resolvedContext
        };
      }
    }

    return null;
  }, [context, recordContextReference]);

  // è·å–ä¸Šä¸‹æ–‡æ‘˜è¦ï¼ˆç”¨äºå‘é€ç»™AIï¼‰
  const getContextSummary = useCallback(() => {
    const recentMessages = context.recentMessages.slice(-6); // æœ€è¿‘3è½®å¯¹è¯
    const recentActions = context.recentActions.slice(-5); // æœ€è¿‘5ä¸ªåŠ¨ä½œ
    
    let summary = '';
    
    if (recentMessages.length > 0) {
      summary += 'æœ€è¿‘çš„å¯¹è¯:\n';
      recentMessages.forEach(msg => {
        summary += `${msg.type === 'user' ? 'ç”¨æˆ·' : 'AI'}: ${msg.content}\n`;
      });
      summary += '\n';
    }
    
    if (recentActions.length > 0) {
      summary += 'æœ€è¿‘çš„æ“ä½œ:\n';
      recentActions.forEach(action => {
        summary += `${action.action}: ${action.taskText || 'æ— å…·ä½“ä»»åŠ¡'}\n`;
      });
      summary += '\n';
    }
    
    if (context.lastAddedTask) {
      summary += `æœ€åæ·»åŠ çš„ä»»åŠ¡: "${context.lastAddedTask.text}" (ID: ${context.lastAddedTask.id})\n`;
    }
    
    return summary;
  }, [context]);

  // æ¸…é™¤è®°å¿†ï¼ˆå¯é€‰åŠŸèƒ½ï¼‰
  const clearMemory = useCallback(() => {
    setMemory([]);
    setContext({
      recentActions: [],
      recentMessages: []
    });
  }, []);

  return {
    memory,
    context,
    addMemoryEntry,
    recordUserMessage,
    recordAIResponse,
    recordAction,
    recordContextReference,
    resolveContextReference,
    getContextSummary,
    clearMemory
  };
} 